	var {
		log
	} = console
	import {
		publicing
	} from "@/api/api.js"
	import {
		wxloginurl
	} from '@/api/request.js'
	// 引入提示组件
	import HMmessages from "@/components/HM-messages/HM-messages.vue"
	//引入类
	// let logins = require('../login/login.js')
	export default {
		components: {HMmessages},
		data() {
			return {
				modaishow: false
			}
		},

		methods: {
			// 显示
			init() {
				this.modaishow = true
			},
			// 取消
			messcancel() {
				this.modaishow = false
			},

			//获取头像和昵称
			getUserInfo(event) {
				log(event)
				if (event.detail.userInfo) {
					let wxing = event.detail.userInfo
					this.wxCode(wxing.avatarUrl, wxing.nickName)
				}
				this.messcancel()
			},
			//获取code
			wxCode(avatarUrl, nickName) {
				wx.login({
					success: (res) => {
						log(res)
						let code = res.code
						this.wxLogin(avatarUrl, nickName, code)
					},
					fail: (err) => {
						log(err)
					}
				})
			},
			//请求后端登录
			wxLogin(avatarUrl, nickName, code) {
				let data = {
					appid: 'wx81a0f57187f6fda9',
					secret: 'dfb0542f4581f6b65e94dd93bf6de42d',
					avatarUrl,
					nickName,
					code
				}
				publicing(wxloginurl, data)
					.then((res) => {
						console.log(res)
						if (res.data.msg === 'success') {
							//存入本地
							uni.setStorageSync('usermen', res.data.datas)
							let succ = '登录成功'
							let ico = 'success'
							this.tIps(succ,ico)
						}
					})
					.catch((err) => {
						log(err)
						let succ = '登录失败'
						let ico = 'error'
						this.tIps(succ,ico)
					})
			},
         // 提示
         	tIps(succ,ico){
         			this.HMmessages.show(succ,{icon:ico,fontColor:"#ffffff",iconColor:"#ffffff",background:"rgba(102,0,51,0.8)"})
         		}

		}
	}
